.copyright__link {
  display: flex;
  justify-content: space-between;
  line-height: 0;
  text-decoration: none;
  align-items: center;
  padding: 28px 0 32px;
  box-sizing: border-box;
}

.copyright__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: $long-text;
}

.copyright__logo {
  fill: #666666;
}

@media (min-width: $tablet-width) {
  .copyright__link {
    padding: 0;
  }
  .copyright__text {
    margin-right: 22px;
  }
}
