.gift {
  padding: 0 20px;
}

.gift__wrapper {
  padding: 58px 61px 43px 58px;
  background-image: url("../img/background/gift-cat.jpg");
  text-align: center;
  box-sizing: border-box;
  line-height: 0;
}

.gift__icon {
  margin-bottom: 48px;
}

.gift__description {
  margin: 0;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: $main-bg;
}

@media (min-width: $tablet-width) {
  .gift {
    padding: 0 30px;
  }

  .gift__wrapper {
    padding: 57px 124px 61px 77px;
    background-image: url("../img/background/gift-cat-tablet.jpg");
    text-align: center;
    box-sizing: border-box;
    line-height: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .gift__icon {
    margin-bottom: 0;
    margin-right: 265px;
    display: block;
    width: 80px;
    height: 82px;
  }

  .gift__description {
    width: 161px;
    text-align: left;
  }
}

@media (min-width: $desktop-width) {
  .gift {
    padding: 0;
  }

  .gift__wrapper {
    padding: 56px 43px 42px 41px;
    background-image: url("../img/background/gift-cat.jpg");
    text-align: center;
    box-sizing: border-box;
    line-height: 0;
    display: inline-block;
    align-items: center;
  }

  .gift__icon {
    margin: 0 auto;
    display: block;
    width: 80px;
    height: 82px;
  }

  .gift__description {
    width: 161px;
    text-align: center;
    margin-top: 46px;
  }
}
